body {
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0; }

.page-header {
  font-weight: 900;
  text-align: center; }

.card-container::after {
  clear: both;
  content: "";
  display: block; }

.card-panel {
  padding: 0 10px; }
  .card-panel::after {
    clear: both;
    content: "";
    display: block; }
  @media screen and (min-width: 1024px) {
    .card-panel {
      float: left;
      width: 50%; }
      .card-panel:nth-child(odd) {
        text-align: right; }
      .card-panel:nth-child(event) {
        text-align: left; } }

.card {
  background: black;
  border: 4px solid black;
  border-radius: 10px;
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.5);
  box-sizing: border-box;
  margin: 0 auto 50px;
  max-width: 600px;
  width: 100%; }
  @media screen and (min-width: 1024px) {
    .card {
      display: inline-block;
      margin-bottom: 20px; } }

.card-body {
  background: black;
  padding: 1px; }
  .card-body .row:last-child {
    border-radius: 0 0 10px 10px;
    overflow: hidden; }
  .bingoed .card-body {
    position: relative; }
    .bingoed .card-body::before {
      background: rgba(255, 255, 255, 0.5);
      content: '';
      display: block;
      z-index: 2;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0; }
    .bingoed .card-body::after {
      content: 'BINGO!';
      font-size: 80px;
      font-weight: 900;
      z-index: 3;
      position: absolute;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%) rotate(-15deg);
      -moz-transform: translate(-50%, -50%) rotate(-15deg);
      -ms-transform: translate(-50%, -50%) rotate(-15deg);
      -o-transform: translate(-50%, -50%) rotate(-15deg);
      transform: translate(-50%, -50%) rotate(-15deg); }
      @media screen and (min-width: 768px) {
        .bingoed .card-body::after {
          font-size: 160px; } }

.card-header {
  border: 2px solid black;
  border-radius: 10px 10px 0 0;
  border-width: 2px 2px 0;
  color: white;
  padding: 20px; }
  .card-header::after {
    clear: both;
    content: "";
    display: block; }
  .dems .card-header {
    background: #0057b8; }
  .repubs .card-header {
    background: #e4002b; }

.card-title {
  float: left;
  font-weight: 900;
  margin: 0;
  letter-spacing: 2px;
  text-transform: uppercase; }

.card-controls {
  float: right; }
  .card-controls .btn {
    background: none;
    border: 2px solid white;
    border-radius: 50px;
    color: white;
    cursor: pointer;
    margin-left: 10px; }
    .card-controls .btn:hover {
      background: rgba(255, 255, 255, 0.2); }
    .card-controls .btn[disabled] {
      opacity: .5; }

.row {
  display: -webkit-box;
  display: -moz-box;
  display: box;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flexbox;
  display: flex; }

.bingo-square {
  background: white;
  border: 1px solid black;
  box-sizing: border-box;
  cursor: pointer;
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 10px;
  font-weight: 400;
  height: 50px;
  width: 20%;
  -webkit-box-align: center;
  -moz-box-align: center;
  box-align: center;
  -webkit-align-items: center;
  -moz-align-items: center;
  -ms-align-items: center;
  -o-align-items: center;
  align-items: center;
  -ms-flex-align: center;
  display: -webkit-box;
  display: -moz-box;
  display: box;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flexbox;
  display: flex; }
  @media screen and (min-width: 768px) {
    .bingo-square {
      font-size: 16px;
      height: 100px; } }
  .bingo-square.selected {
    position: relative; }
    .bingo-square.selected::after {
      content: 'X';
      font-family: 'Roboto', sans-serif;
      font-size: 40px;
      font-weight: 900;
      opacity: .5;
      position: absolute;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
      -moz-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
      -o-transform: translate(-50%, -50%);
      transform: translate(-50%, -50%); }
      @media screen and (min-width: 768px) {
        .bingo-square.selected::after {
          font-size: 80px; } }
      .dems .bingo-square.selected::after {
        color: #0057b8; }
      .repubs .bingo-square.selected::after {
        color: #e4002b; }
  .bingo-square span {
    display: block;
    overflow: hidden;
    text-align: center;
    text-overflow: ellipsis;
    width: 100%; }

.dems .free {
  background: #0057b8;
  color: white; }

.repubs .free {
  background: #e4002b;
  color: white; }

.page-footer {
  text-align: center; }
